import React from "react"

function getServerVariableValueFromLocalStorage(variableName){
  if(!window.localStorage){
    console.log("Unexpected error: window.localStorage is null")
    return null
  }
  let serverVariable = window.localStorage.getItem('server_variables')
  if (serverVariable === null){
    return null
  }
  let serverVariableJSON = JSON.parse(serverVariable)
  if (!serverVariableJSON.hasOwnProperty(variableName)){
    return null
  }
  return serverVariableJSON[variableName]
}

export default class AuthorizationUrl extends React.Component {
  urlType;
  render() {
    let url = getAuthUrl(this.props.schema, this.props.urlType)//"authorizationUrl" / "tokenUrl"
    return <code>{ url }</code>
  }
}

export function getAuthUrl(schema, urlType) {
  let url = schema.get(urlType).toString()
  if (url === null ||  url.length===0){
    return url
  }

  if (url[0]==='/'){
    // if url starts with '/', combine with server-url as the full path
    let srvUrl = getServerUrl(schema)
    return srvUrl + url
  }
  // otherwise, handle url as an absolute url path
  let variables = [...url.matchAll("(?<={)[^}]+")]
  if(variables.length > 0){
    variables.forEach(variable => {
      // try to get variable value from localstorage
      let paramName = variable.toString()
      let paramValue = getServerVariableValueFromLocalStorage(paramName)
      if (paramValue === null){
        // if there is no paramValue found from local storage
        // try to get value by DOM
        let input = document.getElementsByTagName("input")
        for (let i=0;i<input.length;i++){
          if (input[i].getAttribute("data-variable") === paramName){
            paramValue = input[i].value
            break
          }
        }
      }
      if (paramValue !== "" &&  paramValue!== null){
        url = url.replace("{"+paramName+"}",paramValue)
      }
    })
  }
  return url
}

export function getServerUrl(schema) {
  // TODO : change the strategy for getting server url
  //console.log(schema.get("servers"))
  let selector = "#swagger-ui > section > div.swagger-ui > div:nth-child(2) > div.scheme-container > section > div:nth-child(1) > div > div > div > code"
 // let input = document.getElementsByTagName("input")
  let temp = document.querySelector(selector)

  return temp.innerText
}
