import React from "react"
import {OrderedMap} from "immutable"
import PropTypes from "prop-types"
import ImPropTypes from "react-immutable-proptypes"

function getServerVariableValueFromLocalStorage(variableName){
  if(!window.localStorage){
    console.log("Unexpected error: window.localStorage is null")
    return null
  }
  let serverVariable = window.localStorage.getItem('server_variables')
  if (serverVariable === null){
    return null
  }
  let serverVariableJSON = JSON.parse(serverVariable)
  if (!serverVariableJSON.hasOwnProperty(variableName)){
    return null
  }
  return serverVariableJSON[variableName]
}

function setServerVariableValueToLocalStorage(variableName,variableValue){
  let serverVariableJSON = {}
  if(!window.localStorage){
    return console.log("Unexpected error: window.localStorage is null")
  }
  let serverVariable = window.localStorage.getItem("server_variables")
  if (serverVariable) {
    serverVariableJSON = JSON.parse(serverVariable)
  }
  serverVariableJSON[variableName] = variableValue
  window.localStorage.setItem("server_variables",JSON.stringify(serverVariableJSON))
}


export default class Servers extends React.Component {

  static propTypes = {
    servers: ImPropTypes.list.isRequired,
    currentServer: PropTypes.string.isRequired,
    setSelectedServer: PropTypes.func.isRequired,
    setServerVariableValue: PropTypes.func.isRequired,
    getServerVariable: PropTypes.func.isRequired,
    getEffectiveServerValue: PropTypes.func.isRequired
  }

  componentDidMount() {
    let { servers, currentServer } = this.props

    if(currentServer) {
      return
    }

    //fire 'change' event to set default 'value' of select
    this.setServer(servers.first().get("url"))

  }

  componentWillReceiveProps(nextProps) {
    let {
      servers,
      setServerVariableValue,
      getServerVariable
    } = this.props

   // if(this.props.currentServer !== nextProps.currentServer) {
    let currentServerDefinition = servers
      .find(v => v.get("url") === nextProps.currentServer)

    if(!currentServerDefinition) {
      return this.setServer(servers.first().get("url"))
    }

    let currentServerVariableDefs = currentServerDefinition.get("variables") || OrderedMap()

    currentServerVariableDefs.map((val, key) => {
      let currentValue = getServerVariable(nextProps.currentServer, key)
      // only set the default value if the user hasn't set one yet.
      // once the server variable value has been set by user, it will
      // be stored in the local storage of the browser.
      if(!currentValue) {
        // try to get variable value from local storage firstly
        let value = getServerVariableValueFromLocalStorage(key)
        if (value === null){
          value = val.get("default") || ""
          setServerVariableValueToLocalStorage(key,value)
        }
        setServerVariableValue({
          server: nextProps.currentServer,
          key,
          val: value
        })
      }
    })
  }

  getServerVariableValue(currentServer, name){
    let {getServerVariable} = this.props
    return getServerVariable(currentServer, name)
  }

  onServerChange =( e ) => {
    this.setServer( e.target.value )

    // set default variable values
  }

  onServerVariableValueChange = ( e ) => {
    let {
      setServerVariableValue,
      currentServer
    } = this.props

    let variableName = e.target.getAttribute("data-variable")
    let newVariableValue = e.target.value
    // store the data variable into localStorage
    setServerVariableValueToLocalStorage(variableName,newVariableValue)
    if(typeof setServerVariableValue === "function") {
      setServerVariableValue({
        server: currentServer,
        key: variableName,
        val: newVariableValue
      })
    }
  }

  setServer = ( value ) => {
    let { setSelectedServer } = this.props

    setSelectedServer(value)
  }

  render() {
    let { servers,
      currentServer,
      getServerVariable,
      getEffectiveServerValue
    } = this.props


    let currentServerDefinition = servers.find(v => v.get("url") === currentServer) || OrderedMap()

    let currentServerVariableDefs = currentServerDefinition.get("variables") || OrderedMap()

    let shouldShowVariableUI = currentServerVariableDefs.size !== 0

    return (
      <div className="servers">
        <label htmlFor="servers">
          <select onChange={ this.onServerChange }>
            { servers.valueSeq().map(
              ( server ) =>
              <option
                value={ server.get("url") }
                key={ server.get("url") }>
                { server.get("url") }
                { server.get("description") && ` - ${server.get("description")}` }
              </option>
            ).toArray()}
          </select>
        </label>
        { shouldShowVariableUI ?
          <div>

            <div className={"computed-url"}>
              Computed URL:
              <code>
                {getEffectiveServerValue(currentServer)}
              </code>
            </div>
            <h4>Server variables</h4>
            <table>
              <tbody>
                {
                  currentServerVariableDefs.map((val, name) => {
                    return <tr key={name}>
                      <td>{name}</td>
                      <td>
                        { val.get("enum") ?
                          <select data-variable={name} onChange={this.onServerVariableValueChange}>
                            {val.get("enum").map(enumValue => {
                              return <option
                                selected={enumValue === getServerVariable(currentServer, name)}
                                key={enumValue}
                                value={enumValue}>
                                {enumValue}
                              </option>
                            })}
                          </select> :
                          <input
                            type={"text"}
                            value={this.getServerVariableValue(currentServer, name) || ""}
                            onChange={this.onServerVariableValueChange}
                            data-variable={name}
                            />
                        }
                      </td>
                    </tr>
                  })
                }
              </tbody>
            </table>
          </div>: null
        }
      </div>
    )
  }
}
